.container {
  padding: 16px;
}

.card {
  position: relative;
  border-radius: 8px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(155, 35, 8, 0.3);
          box-shadow: 0 2px 4px 0 rgba(155, 35, 8, 0.3);
  font-family: 'Circular Std', sans-serif;
  font-weight: var(--font-weight-regular);
  -webkit-transition: -webkit-box-shadow 150ms ease-out;
  transition: -webkit-box-shadow 150ms ease-out;
  -o-transition: box-shadow 150ms ease-out;
  transition: box-shadow 150ms ease-out;
  transition: box-shadow 150ms ease-out, -webkit-box-shadow 150ms ease-out;
}

.cardSelected::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  bottom: 0;
  left: 50%;
  width: calc(100% + 12px);
  height: calc(100% + 12px);
  border: 4px solid var(--colour-primary);
  border-radius: 13px;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.card:hover {
  -webkit-box-shadow: 0 11px 24px 0 rgba(155, 35, 8, 0.3);
          box-shadow: 0 11px 24px 0 rgba(155, 35, 8, 0.3);
}

.cardImage {
  height: 152px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  background: -webkit-linear-gradient(233deg, var(--colour-white), #fff7f5);
  background: -o-linear-gradient(233deg, var(--colour-white), #fff7f5);
  background: linear-gradient(217deg, var(--colour-white), #fff7f5);
  background-size: cover;
}

.card:hover .cardOverlay {
  opacity: 1;
}

.cardOverlay {
  opacity: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 100%;
  padding-right: 24px;
  padding-left: 24px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  background-color: rgba(255, 103, 68, 0.85);
  -webkit-transition: opacity 150ms ease-out;
  -o-transition: opacity 150ms ease-out;
  transition: opacity 150ms ease-out;
}

.cardButton {
  display: block;
  padding: 8px 16px;
  border-radius: 40px;
  border: solid 3px var(--colour-white);
  font-size: 14px;
  color: var(--colour-white);
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  -webkit-transition: background-color 150ms ease-out;
  -o-transition: background-color 150ms ease-out;
  transition: background-color 150ms ease-out;
}

.cardButton:hover {
  background-color: rgba(255, 255, 255, 0.25);
}

.deleteButton::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: var(--colour-white);
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.cardBody {
  overflow-y: hidden;
  position: relative;
  height: 176px;
  padding: 24px 16px;
}

.cardBody::after {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 24px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  background-color: var(--colour-white);
}

.date {
  margin-bottom: 16px;
  color: var(--colour-grey);
  font-size: 14px;
}

.name {
  color: var(--colour-dark-grey);
  font-size: 20px;
  text-decoration: none;
  -webkit-transition: color 150ms linear;
  -o-transition: color 150ms linear;
  transition: color 150ms linear;
}

.name:focus,
.name:hover {
  color: var(--colour-primary);
}



